	module.exports = {"data":{"markdownRemark":{"html":"<p>Font family is controlled by the <code>font-family</code> CSS attribute, like this <code>h1 {font-family: Sans-serif;}</code></p>\n<p>It allows you to pick a font to display to the user.</p>\n<p>More font-family examples can be viewed at <a href='https://developer.mozilla.org/en-US/docs/Web/CSS/font-family' target='_blank' rel='nofollow'>MDN</a></p>\n<p><strong><em>Note</em></strong>: Keep in mind that different computers and browsers will have different fonts enabled. Commonly available fonts are called <em>Web Safe</em></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code> p {\n    font-size: 16px;\n    font-family: Monospace;\n  }</code></pre>\n      </div>","fields":{"slug":"/certificates/set-the-font-family-of-an-element/"},"frontmatter":{"title":"Set the Font Family of an Element"}}},"pathContext":{"slug":"/certificates/set-the-font-family-of-an-element/"}}

